import React, { useState } from 'react';
import { Tabs } from 'antd';


import ProfileHeader from '../../profileHeader/ProfileHeader';
import EditProfile from '../../editProfile/EditProfile';
import ChangePass from '../../changePass/ChangePass';
import ChangeAvatar from '../../changeAvatar/ChangeAvatar';
import './Settings.scss';

const { TabPane } = Tabs;

const Settings = () => {
    return (
        <>
            <ProfileHeader/>
            <div className="settings">
                <div className="container">
                    <div className="settings__in panel">
                        <div className="settings__body">
                            <Tabs
                                defaultActiveKey='1'
                                tabPosition='left'
                                >
                                <TabPane  tab={' '} key={1}>
                                    <EditProfile/>
                                </TabPane> 
                                <TabPane  tab={' '} key={2}>
                                    <ChangePass/>
                                </TabPane> 
                                <TabPane  tab={' '} key={3}>
                                    <ChangeAvatar/>
                                </TabPane> 
                                <TabPane  tab={'.   SMS'} key={4}>
                                    <h2>.   SMS</h2>
                                </TabPane> 
                                <TabPane  tab={'Push-'} key={5}>
                                    <h2>Push-</h2>
                                </TabPane> 
                                <TabPane  tab={' '} key={6}>
                                    <h2> </h2>
                                </TabPane> 
                                <TabPane  tab={'  '} key={7}>
                                    <h2>  </h2>
                                </TabPane> 
                                <TabPane  tab={'  '} key={8}>
                                    <h2>  </h2>
                                </TabPane> 
                                <TabPane  tab={'   Galogram'} key={9}>
                                    <h2>   Galogram</h2>
                                </TabPane> 
                                <TabPane  tab={''} key={10}>
                                    <h2></h2>
                                </TabPane> 
                                <TabPane  tab={'   '} key={11}>
                                    <h2>   </h2>
                                </TabPane> 
                            </Tabs>
                        </div>
                        {/* <div className="settings__main">

                        </div> */}
                    </div>
                </div>
            </div>
        </>
        
    )
} 

export default Settings;